import React, { useState, useEffect, useRef, useCallback } from 'react';
import PropTypes from 'prop-types';
import DSDropdownMenu from '@elliemae/ds-dropdownmenu';
import DSInputMask from '../form/InputMask';
import { InputSearchWrapper } from './components/blocks';
import { createDisabledOption, noAddonOptions } from './components/helper';

const DSZipCodeSearch = ({
  disabled = false,
  value = undefined,
  placeholder = '_____-____',
  mask = [/\d/, /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/],
  onBlur = () => null,
  onChange = () => null,
  onSearch = () => null,
  containerProps = {},
  // noOptions = 'No options',
  selectRightOptions = 'Please select the appropriate city',
  activeCount = 5,
  options = [],
}) => {
  const ref = useRef(null);
  const [searchValue, setValue] = useState();
  const [isOpen, setOpen] = useState(false);
  const [isActive, setActive] = useState(false);
  const [isInputFocus, setInputFocus] = useState(false);
  const [isSelected, setSelected] = useState(false);

  useEffect(() => {
    if (isInputFocus && activeCount <= searchValue.length && !isSelected) {
      setOpen(true);
      setActive(true);
    } else if (!isActive || activeCount > searchValue.length) {
      setOpen(false);
    }
  }, [isInputFocus, isActive, activeCount, searchValue]);
  useEffect(() => {
    setValue(value || '');
  }, [value]);

  const onTypeInInput = useCallback((e) => {
    setValue(e.target.value);
    onSearch(e.target.value);
    setSelected(false);
  }, []);

  const onFocusInput = useCallback(() => {
    setInputFocus(true);
  }, []);

  const onInnerBlur = useCallback(
    (e) => {
      setInputFocus(false);
      onBlur(e);
    },
    [onBlur],
  );
  const onClose = useCallback(() => {
    setActive(false);
  }, []);
  const onSelectItem = useCallback((item) => {
    setValue(item.value);
    onChange(item);
    setSelected(true);
    ref.current.focus();
    setOpen(false);
    onClose();
  }, []);
  return (
    <InputSearchWrapper {...containerProps}>
      <DSDropdownMenu
        isOpen={isOpen && options.length > 0}
        menuProps={{ className: 'input-search-menu' }}
        // onBlur={() => console.log('inblur;')}
        onClose={onClose}
        onSelectMenuItem={onSelectItem}
        options={
          options.length > 0
            ? [
                createDisabledOption(selectRightOptions),
                ...noAddonOptions(options),
              ]
            : [
                // createDisabledOption(noOptions),
              ]
        }
        triggerComponent={
          <DSInputMask
            disabled={disabled}
            innerRef={ref}
            mask={mask || []}
            onBlur={onInnerBlur}
            onChange={onTypeInInput}
            onFocus={onFocusInput}
            placeholder={placeholder}
            value={searchValue}
          />
        }
      />
    </InputSearchWrapper>
  );
};
DSZipCodeSearch.propTypes = {
  className: PropTypes.string,
  containerProps: PropTypes.shape({}),
  disabled: PropTypes.bool,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
  ]),
  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  options: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  ),
  placeholder: PropTypes.string,
  mask: PropTypes.arrayOf(PropTypes.any),
  onSearch: PropTypes.func,
  noOptions: PropTypes.string,
  selectRightOptions: PropTypes.string,
  activeCount: PropTypes.number,
};

export default DSZipCodeSearch;
